"use client";

import { usePaymentStore } from "@/lib/store";
import { Badge } from "@/components/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { format } from "date-fns";
import { fr } from "date-fns/locale";
import { PaymentForm } from "./payment-form";

export function PaymentList() {
  const { payments } = usePaymentStore();

  const getStatusBadgeVariant = (status: string) => {
    switch (status) {
      case "complet":
        return "default";
      case "en_attente":
        return "secondary";
      case "echoue":
        return "destructive";
      case "rembourse":
        return "outline";
      default:
        return "secondary";
    }
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("fr-FR", {
      style: "currency",
      currency: "EUR",
    }).format(amount);
  };

  const formatDate = (date: Date) => {
    return format(date, "dd/MM/yyyy", { locale: fr });
  };

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Liste des Paiements</h2>
        <PaymentForm />
      </div>

      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Référence</TableHead>
              <TableHead>Client</TableHead>
              <TableHead>Montant</TableHead>
              <TableHead>Statut</TableHead>
              <TableHead>Méthode</TableHead>
              <TableHead>Date</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {payments.map((payment) => (
              <TableRow key={payment.id}>
                <TableCell className="font-medium">{payment.reference}</TableCell>
                <TableCell>{payment.client_nom}</TableCell>
                <TableCell>{formatCurrency(payment.montant)}</TableCell>
                <TableCell>
                  <Badge variant={getStatusBadgeVariant(payment.statut)}>
                    {payment.statut}
                  </Badge>
                </TableCell>
                <TableCell>{payment.methode}</TableCell>
                <TableCell>{formatDate(payment.date_creation)}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      {payments.length === 0 && (
        <div className="text-center py-8">
          <p className="text-muted-foreground">Aucun paiement trouvé</p>
        </div>
      )}
    </div>
  );
}
