import { create } from 'zustand';
import { Payment, PaymentStats } from '@/types/payment';

interface PaymentStore {
  payments: Payment[];
  stats: PaymentStats;
  addPayment: (payment: Omit<Payment, 'id' | 'date_creation'>) => void;
  updatePayment: (id: string, updates: Partial<Payment>) => void;
  deletePayment: (id: string) => void;
  getPaymentById: (id: string) => Payment | undefined;
  calculateStats: () => void;
}

export const usePaymentStore = create<PaymentStore>((set, get) => ({
  payments: [],
  stats: {
    total_paiements: 0,
    montant_total: 0,
    paiements_en_attente: 0,
    paiements_complets: 0,
    paiements_echoues: 0,
  },

  addPayment: (paymentData) => {
    const newPayment: Payment = {
      ...paymentData,
      id: crypto.randomUUID(),
      date_creation: new Date(),
    };
    
    set((state) => ({
      payments: [...state.payments, newPayment],
    }));
    
    get().calculateStats();
  },

  updatePayment: (id, updates) => {
    set((state) => ({
      payments: state.payments.map((p) =>
        p.id === id ? { ...p, ...updates } : p
      ),
    }));
    get().calculateStats();
  },

  deletePayment: (id) => {
    set((state) => ({
      payments: state.payments.filter((p) => p.id !== id),
    }));
    get().calculateStats();
  },

  getPaymentById: (id) => {
    return get().payments.find((p) => p.id === id);
  },

  calculateStats: () => {
    const { payments } = get();
    const stats: PaymentStats = {
      total_paiements: payments.length,
      montant_total: payments.reduce((sum, p) => sum + p.montant, 0),
      paiements_en_attente: payments.filter((p) => p.statut === "en_attente").length,
      paiements_complets: payments.filter((p) => p.statut === "complet").length,
      paiements_echoues: payments.filter((p) => p.statut === "echoue").length,
    };
    set({ stats });
  },
}));
