"use client";

import { useState } from "react";
import { usePaymentStore } from "@/lib/store";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

export function PaymentForm() {
  const { addPayment } = usePaymentStore();
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    montant: "",
    description: "",
    client_nom: "",
    client_email: "",
    methode: "carte_credit" as const,
    statut: "en_attente" as const,
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    const newPayment = {
      montant: parseFloat(formData.montant),
      devise: "EUR",
      description: formData.description,
      statut: formData.statut,
      methode: formData.methode,
      client_id: crypto.randomUUID(),
      client_nom: formData.client_nom,
      client_email: formData.client_email,
      reference: `PAY-${new Date().getFullYear()}-${String(Math.floor(Math.random() * 1000)).padStart(3, '0')}`,
    };

    addPayment(newPayment);
    
    // Réinitialiser le formulaire
    setFormData({
      montant: "",
      description: "",
      client_nom: "",
      client_email: "",
      methode: "carte_credit",
      statut: "en_attente",
    });
    
    setOpen(false);
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button>Ajouter un paiement</Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Ajouter un nouveau paiement</DialogTitle>
          <DialogDescription>
            Remplissez les informations du paiement à ajouter.
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit}>
          <div className="grid gap-4 py-4">
            <div className="grid gap-2">
              <Label htmlFor="montant">Montant (EUR)</Label>
              <Input
                id="montant"
                type="number"
                step="0.01"
                placeholder="150.50"
                value={formData.montant}
                onChange={(e) => setFormData({ ...formData, montant: e.target.value })}
                required
              />
            </div>
            
            <div className="grid gap-2">
              <Label htmlFor="description">Description</Label>
              <Textarea
                id="description"
                placeholder="Description du paiement"
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                required
              />
            </div>
            
            <div className="grid gap-2">
              <Label htmlFor="client_nom">Nom du client</Label>
              <Input
                id="client_nom"
                placeholder="Jean Dupont"
                value={formData.client_nom}
                onChange={(e) => setFormData({ ...formData, client_nom: e.target.value })}
                required
              />
            </div>
            
            <div className="grid gap-2">
              <Label htmlFor="client_email">Email du client</Label>
              <Input
                id="client_email"
                type="email"
                placeholder="jean.dupont@email.com"
                value={formData.client_email}
                onChange={(e) => setFormData({ ...formData, client_email: e.target.value })}
                required
              />
            </div>
            
            <div className="grid gap-2">
              <Label htmlFor="methode">Méthode de paiement</Label>
              <Select
                value={formData.methode}
                onValueChange={(value) => setFormData({ ...formData, methode: value as any })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="carte_credit">Carte de crédit</SelectItem>
                  <SelectItem value="virement_bancaire">Virement bancaire</SelectItem>
                  <SelectItem value="paypal">PayPal</SelectItem>
                  <SelectItem value="especes">Espèces</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="grid gap-2">
              <Label htmlFor="statut">Statut</Label>
              <Select
                value={formData.statut}
                onValueChange={(value) => setFormData({ ...formData, statut: value as any })}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="en_attente">En attente</SelectItem>
                  <SelectItem value="complet">Complet</SelectItem>
                  <SelectItem value="echoue">Échoué</SelectItem>
                  <SelectItem value="rembourse">Remboursé</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
          <DialogFooter>
            <Button type="submit">Ajouter le paiement</Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
